<template>
  <div class="NavMenu">
    <el-card>
      <el-menu router default-active="2" class="el-menu-vertical-demo"  :unique-opened="true">
        <el-card>
                <template v-for="(item,index) in menus">
                  <div :index="index + ''"  :key="index" v-if="!item.hidden">
                    <template v-for="(item,index) in item.children">
                      <el-submenu :index="index + ''"  :key="index" v-if="!item.hidden">
                        <template slot="title">
                          <i :class="item.iconClass" style=" padding-right: 10px; padding-left: 5px;"></i>
                          <span>{{item.name}}</span>
                        </template>
                        <el-menu-item-group  v-for="(child,index) in item.children" :key="index">
                          <el-menu-item :index="child.path"  style="padding-left: 0;">
                            <i :class="child.iconClass"></i>
                            {{child.name}}
                          </el-menu-item>
                        </el-menu-item-group>
                      </el-submenu>
                    </template>
            </div>
          </template>
        <el-menu-item style="padding-left: 0;">
          <i class="el-icon-setting"></i>
          <span slot="title" style="padding-left: 0;" @click="qx()">权限管理</span>
        </el-menu-item>
        </el-card>
      </el-menu>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      menus:[]
    };
  },
  created() {
    this.menus=[...this.$router.options.routes]
  },
  methods: {
    qx(){
      this.$message({message:'无操作权限',type:'warning'})
    },

  }
}
</script>

<style lang="scss">
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 95%;
  min-height: 100%;
}
.NavMenu {
  width: 100%;
  height: 95%;
  .el-card{
    margin: 5% 0 0 0;
    height: 100%;
    .el-submenu .el-menu-item {
      height: 50px;
      line-height: 50px;
      padding: 0;
      min-width: 0;
    }
    .el-submenu__title{
      margin: 0 0 0 -20px;
    }
    .el-submenu__icon-arrow {
      right: 0;
    }
  }
}
.el-aside {
overflow: auto;
box-sizing: border-box;
flex-shrink: 0;
padding-left: 20px;
}

//取消悬停颜色
.el-submenu__title:hover, .el-menu-item:focus, .el-menu-item:hover{
  background-color: rgba(255, 255, 255, 0.97);
  color: #409EFF;
